<template>
<div>
  <custom-menu></custom-menu>
  <v-container>
    <v-form
      ref="form"
      v-model="valid"
      lazy-validation
    >
      <v-text-field
        v-model="name"
        :counter="10"
        :rules="nameRules"
        label="Name"
        required
      ></v-text-field>

      <v-text-field
        v-model="email"
        :rules="emailRules"
        label="E-mail"
        required
      ></v-text-field>

      <v-checkbox
        v-model="checkbox"
        :rules="[v => !!v || 'You must agree to continue!']"
        label="Do you agree?"
        required
      ></v-checkbox>

      <v-btn
        :disabled="dialog"
        :loading="dialog"
        color="blue"
        @click="validate"
      >
        Submit
      </v-btn>
      </v-form>
    </v-container>
  </div>
</template>

<script>
import CustomMenu from '../components/Menu.vue'
  export default {
    components:{
      CustomMenu
    },
    data: () => ({
      dialog: false,
      valid: true,
      name: '',
      nameRules: [
        v => !!v || 'Name is required',
        v => (v && v.length <= 10) || 'Name must be less than 10 characters'
      ],
      email: '',
      emailRules: [
        v => !!v || 'E-mail is required',
        v => /.+@.+/.test(v) || 'E-mail must be valid'
      ],
      checkbox: false
    }),
     watch: {
      dialog (val) {
        if (!val) return

        setTimeout(() => (this.dialog = false), 8000)
      }
    },
    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          this.dialog = true;
          console.log(this.name);
          console.log(this.email);
        }
      },
    }
  }
</script>



