import Vue from 'vue'
import Router from 'vue-router'
//Components and Pages
import Home from '@/views/home'
import Login from '@/views/login'
import About from '@/views/about'



Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/about',
      name: 'About',
      component: About
    },
  ]
})



