<template>
  <div>

 <custom-menu></custom-menu>
    <v-toolbar tabs>
      <v-tabs

        v-model="tabs"
        fixed-tabs
        color="transparent"
      >
        <v-tabs-slider></v-tabs-slider>
        <v-tab href="#mobile-tabs-5-1" class="primary--text">
          <v-icon>phone</v-icon>
        </v-tab>

        <v-tab href="#mobile-tabs-5-2" class="primary--text">
          <v-icon>favorite</v-icon>
        </v-tab>

        <v-tab href="#mobile-tabs-5-3" class="primary--text">
          <v-icon>account_box</v-icon>
        </v-tab>
      </v-tabs>
    </v-toolbar>

    <v-tabs-items v-model="tabs" class="white elevation-1">
      <v-tab-item
        v-for="i in 3"
        :value="'mobile-tabs-5-' + i"
        :key="i"
      >
        <div v-if="i === 1">
          <v-card v-for="(other, index) in getOtherFromStore" :key="index">
            {{other}}
          </v-card>
        </div>
        <v-card v-else-if="i !== 1">
          <v-card-text v-for="(item, index) in getTagsFromStore" :key="index">
            {{item}} -> {{index}}
          </v-card-text>
        </v-card>
      </v-tab-item>
    </v-tabs-items>
  </div>
</template>

<script>
import CustomMenu from '../components/Menu.vue'
  export default {
    components:{
      CustomMenu
    },
    data() {
      return {
        tabs: null,
        //text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.'
      }
    },
    computed: {
      getTagsFromStore(){
        return this.$store.getters.getTags;
        //this.$store.dispatch('showMenu', true);
      },
      getOtherFromStore(){
        this.$store.dispatch('ModuleOther/setOthersFromFirebaseToStore');
        return this.$store.getters['ModuleOther/getOthers'];
      }
    }
  }
</script>



