<template>
  <v-layout>
    <v-toolbar-side-icon @click.stop="drawer = !drawer" class="burger-menu">
    </v-toolbar-side-icon>
    <v-spacer></v-spacer>
    <v-toolbar-title class="app-title">Boxing App</v-toolbar-title>

    <v-navigation-drawer
      v-model="drawer"
      absolute
      temporary
    >
      <v-list class="pa-1">
        <v-list-tile avatar>
          <v-list-tile-avatar>
            <img src="https://randomuser.me/api/portraits/men/85.jpg">
          </v-list-tile-avatar>

          <v-list-tile-content>
            <v-list-tile-title>John Leider</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>

      <v-list class="pt-0" dense>
        <v-divider></v-divider>

        <v-list-tile
          v-for="item in navs"
          :key="item.title"
        >
          <v-list-tile-action>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-tile-action>

          <!-- <v-list-tile-content>  -->
            <router-link class="nav-item" :to="{path: item.route }">
              {{ item.title }}
            </router-link>

          <!-- </v-list-tile-content> -->
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>
  </v-layout>
</template>


<script>
  export default {
    data () {
      return {
        drawer: null,
        navs: [
          { title: 'Home', icon: 'dashboard', route: '/' },
          { title: 'About', icon: 'question_answer', route: 'about' },
          { title: 'Login', icon: 'https', route: 'login' }
        ]
      }
    }
  }
</script>

<style>
.app-title{
  text-align: center;
  line-height: 2.2em;
  padding-right: 2em
}
.nav-item{
  text-decoration: none;
  color: black;
}
</style>



