import Vue from 'vue'
import firebase from '../../firebase'
Vue.use(firebase)

export default {
  namespaced: true,
  state:{
    others:{},
  },

  getters:{
    getOthers(state){
      return state.others;
    }
  },

  mutations:{
    setOthersFromFirebaseToStore(state, payload){
      state.others = payload;
    }
  },

  actions:{
    setOthersFromFirebaseToStore(context){
      firebase.database.ref('others').on('value', function(snapshot){
          context.commit('setOthersFromFirebaseToStore', snapshot.val());
      })
    }
  }
}
