import Vue from 'vue'
import Vuex from 'vuex'
import ModuleOther from './other'

Vue.use(Vuex)

export default new Vuex.Store({
  state:{
    tags:['a1', 'a2'],

  },
  getters:{
    getTags(state){
      return state.tags;
    }
  },
  mutations:{
    setTags(state, payload){
      state.tags = payload;
    }
  },
  actions:{
    setTags(context, arr){
      context.commit('setTags', arr);
    }
  },
  modules:{
    ModuleOther
  }
})



